  color: #ffffff;
  text-decoration: none; }

.nav__toggle {
  position: relative;
  top: 20px;
  display: block;
  width: 50px;
  height: 25px;
  background: rgba(40, 54, 69, 0.3);
  border: none;
  outline: none;
  cursor: pointer; }

.listen-nav--closed .nav__toggle {
  background: rgba(40, 54, 69, 0.3); }

.listen-nav--closed .nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff; }

.nav__toggle:active::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
  opacity: 0.7; }

.nav__toggle {
  width: 25px;
  background: rgba(40, 54, 69, 0.3); }
  .nav__toggle::before, .nav__toggle::after {
    content: "";
    position: absolute;
    top: 10px;
    right: -2px;
    width: 30px;
    height: 5px;
    background-color: #ffffff;
    border-radius: 20px; }
  .nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none; }
  .nav__toggle::after {
    transform: rotate(-45deg); }

.nav__toggle:active::before, .nav__toggle:active::after {
  background-color: #d22856;
  opacity: 0.7; }

@media (max-width: 959px) {
  .nav--closed .nav__wrapper-list {
    display: none; }
  .nav--opened .nav__wrapper-list,
  .nav--nojs .nav__wrapper-list {
